.ant-dropdown-menu {
	border: 1px solid @dropdown-border-color;
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.1);
	background: @dropdown-bg-color;
	border-radius: 2px;
}
.ant-dropdown-menu-item-divider, .ant-dropdown-menu-submenu-title-divider {
	background: @dropdown-divider-color;
}
.ant-dropdown-menu-item > a, .ant-dropdown-menu-submenu-title > a, .ant-dropdown-menu-item, .ant-dropdown-menu-submenu-title, .ant-dropdown-menu-item .ant-dropdown-menu-submenu-arrow-icon, .ant-dropdown-menu-submenu-title .ant-dropdown-menu-submenu-arrow-icon {
	color: @dropdown-content-color;
}
.ant-dropdown-menu-item > a:hover, .ant-dropdown-menu-submenu-title > a:hover, .ant-dropdown-menu-item:hover, .ant-dropdown-menu-submenu-title:hover {
	background: @dropdown-bg-hover;
}
.ant-dropdown-menu-item-disabled, .ant-dropdown-menu-item-disabled, .ant-dropdown-menu-submenu.ant-dropdown-menu-submenu-disabled .ant-dropdown-menu-submenu-title, .ant-dropdown-menu-submenu.ant-dropdown-menu-submenu-disabled .ant-dropdown-menu-submenu-title .ant-dropdown-menu-submenu-arrow-icon {
	background: @dropdown-bg-disabled;
	color: @dropdown-content-disabled;
}
.ant-dropdown-menu-item-disabled:hover {
	background: @dropdown-bg-color;
	color: @dropdown-content-disabled;
}


@dropdown-prefix-cls: ~'@{ant-prefix}-dropdown';

.@{dropdown-prefix-cls} {
	.reset-component;

	position: absolute;
	top: -9999px;
	left: -9999px;
	z-index: @zindex-dropdown;
	display: block;

	&::before {
		position: absolute;
		top: -7px;
		right: 0;
		bottom: -7px;
		left: -7px;
		z-index: -9999;
		opacity: 0.0001;
		content: ' ';
	}

	&-wrap {
		position: relative;

		.@{ant-prefix}-btn > .@{iconfont-css-prefix}-down {
			.iconfont-size-under-12px(10px);
		}

		.@{iconfont-css-prefix}-down::before {
			transition: transform 0.2s;
		}
	}

	&-wrap-open {
		.@{iconfont-css-prefix}-down::before {
			transform: rotate(180deg);
		}
	}

	&-hidden,
	&-menu-hidden {
		display: none;
	}

	&-menu {
		position: relative;
		margin: 0;
		padding: 4px 0;
		text-align: left;
		list-style-type: none;
		background-color: @component-background;
		background-clip: padding-box;
		border-radius: @border-radius-base / 2;
		outline: none;
		box-shadow: @box-shadow-base;
		-webkit-transform: translate3d(0, 0, 0);

		&-item-group-title {
			padding: 5px @control-padding-horizontal;
			color: @text-color-secondary;
			transition: all 0.3s;
		}

		&-submenu-popup {
			position: absolute;
			z-index: @zindex-dropdown;

			> .@{dropdown-prefix-cls}-menu {
				transform-origin: 0 0;
			}
		}

		&-item,
		&-submenu-title {
			clear: both;
			margin: 0;
			padding: 5px @control-padding-horizontal;
			color: @text-color;
			font-weight: normal;
			font-size: @font-size-base;
			line-height: 22px;
			white-space: nowrap;
			cursor: pointer;
			transition: all 0.3s;

			> .anticon:first-child {
				min-width: 12px;
				margin-right: 8px;
			}

			> a {
				display: block;
				margin: -5px -@control-padding-horizontal;
				padding: 5px @control-padding-horizontal;
				color: @text-color;
				transition: all 0.3s;
			}

			&-selected,
			&-selected > a {
				color: @primary-color;
				background-color: @item-active-bg;
			}

			&:hover {
				background-color: @item-hover-bg;
			}

			&-disabled {
				color: @disabled-color;
				cursor: not-allowed;

				&:hover {
					color: @disabled-color;
					background-color: @component-background;
					cursor: not-allowed;
				}
			}

			&-divider {
				height: 1px;
				margin: 4px 0;
				overflow: hidden;
				line-height: 0;
				background-color: @border-color-split;
			}
			.@{dropdown-prefix-cls}-menu-submenu-arrow {
				position: absolute;
				right: @padding-xs;
				&-icon {
					color: @text-color-secondary;
					font-style: normal;
					.iconfont-size-under-12px(10px);
				}
			}
		}

		&-submenu-title {
			padding-right: 26px;
		}

		&-submenu-vertical {
			position: relative;
		}

		&-submenu-vertical > & {
			position: absolute;
			top: 0;
			left: 100%;
			min-width: 100%;
			margin-left: 4px;
			transform-origin: 0 0;
		}

		&-submenu&-submenu-disabled .@{dropdown-prefix-cls}-menu-submenu-title {
			&,
			.@{dropdown-prefix-cls}-menu-submenu-arrow-icon {
				color: @disabled-color;
				background-color: @component-background;
				cursor: not-allowed;
			}
		}
	}

	&.slide-down-enter.slide-down-enter-active&-placement-bottomLeft,
	&.slide-down-appear.slide-down-appear-active&-placement-bottomLeft,
	&.slide-down-enter.slide-down-enter-active&-placement-bottomCenter,
	&.slide-down-appear.slide-down-appear-active&-placement-bottomCenter,
	&.slide-down-enter.slide-down-enter-active&-placement-bottomRight,
	&.slide-down-appear.slide-down-appear-active&-placement-bottomRight {
		animation-name: antSlideUpIn;
	}

	&.slide-up-enter.slide-up-enter-active&-placement-topLeft,
	&.slide-up-appear.slide-up-appear-active&-placement-topLeft,
	&.slide-up-enter.slide-up-enter-active&-placement-topCenter,
	&.slide-up-appear.slide-up-appear-active&-placement-topCenter,
	&.slide-up-enter.slide-up-enter-active&-placement-topRight,
	&.slide-up-appear.slide-up-appear-active&-placement-topRight {
		animation-name: antSlideDownIn;
	}

	&.slide-down-leave.slide-down-leave-active&-placement-bottomLeft,
	&.slide-down-leave.slide-down-leave-active&-placement-bottomCenter,
	&.slide-down-leave.slide-down-leave-active&-placement-bottomRight {
		animation-name: antSlideUpOut;
	}

	&.slide-up-leave.slide-up-leave-active&-placement-topLeft,
	&.slide-up-leave.slide-up-leave-active&-placement-topCenter,
	&.slide-up-leave.slide-up-leave-active&-placement-topRight {
		animation-name: antSlideDownOut;
	}
}

.@{dropdown-prefix-cls}-trigger,
.@{dropdown-prefix-cls}-link {
	> .@{iconfont-css-prefix}.@{iconfont-css-prefix}-down {
		.iconfont-size-under-12px(10px);
	}
}

.@{dropdown-prefix-cls}-link {
	color: rgb(39, 160, 203);
}
.@{dropdown-prefix-cls}-link:hover {
	color: rgb(46, 176, 222);
}
.@{dropdown-prefix-cls}-link:active {
	color: rgb(29, 139, 178);
}

.@{dropdown-prefix-cls}-button {
	white-space: nowrap;

	&.@{ant-prefix}-btn-group > .@{ant-prefix}-btn:last-child:not(:first-child) {
		padding-right: @padding-xs;
		padding-left: @padding-xs;
	}
	.@{iconfont-css-prefix}.@{iconfont-css-prefix}-down {
		.iconfont-size-under-12px(10px);
	}
}

// https://github.com/ant-design/ant-design/issues/4903
.@{dropdown-prefix-cls}-menu-dark {
	&,
	.@{dropdown-prefix-cls}-menu {
		background: @menu-dark-bg;
	}
	.@{dropdown-prefix-cls}-menu-item,
	.@{dropdown-prefix-cls}-menu-submenu-title,
	.@{dropdown-prefix-cls}-menu-item > a {
		color: @text-color-secondary-dark;
		.@{dropdown-prefix-cls}-menu-submenu-arrow::after {
			color: @text-color-secondary-dark;
		}
		&:hover {
			color: @text-color-inverse;
			background: transparent;
		}
	}
	.@{dropdown-prefix-cls}-menu-item-selected {
		&,
		&:hover,
		> a {
			color: @text-color-inverse;
			background: @primary-color;
		}
	}
}
