.ant-tooltip {
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
  }
  
  .ant-tooltip{
    max-width: 100%;
  }
  .ant-tooltip-inner{
    background: #000;
    opacity: 0.8;
    color: #fff;
    min-height: 28px;
    line-height: 24px;
    font-size: 14px;
    padding: 4px;
  }