.theme-dark {
    @body-background: #ecf0f5;

// ------start-------
@nav-top-background: #242d3f; // top 
@nav-left-background: #242d3f; // left 
// ------end---------

// ------start-------
@card-content: #151a24;
// ------end---------

// ------start-------
@text-title: #ffffff; // 
@text-content: #d6d8d8; // 
@text-interaction: #4cd0ff; // 
// ------end---------

// ------start-------
@modal-background: #323849; // 
@modal-border: #454545;
// ------end---------

// ------start-tooltip------
@tooltip-background: rgba(0, 0, 0, 0.6); // tooltip
// ------end-tooltip--------

// ------start-form------
@form-bg: #0c0e13;
@form-border: #454545;
@form-hover-bg: #0c0e13; // 
@form-hover-border: #767676; //  & 
@form-press-bg: #0c0e13; // 
@form-press-border: #27a0cb; // 
@form-error-bg: #0c0e13;
@form-error-border: #cd6363;
@form-disable-bg: #1c222c;
@form-disable-border: #454545;
// ------end-form--------

// ------start-table------
@table-title-bg: #111111;
@table-space-bg-first: #1a202b;
@table-space-bg-second: #151a24;
@table-hover-interaction: #213f4e;
// ------end-table--------
@import './dark';
// @import (multiple) './theme';
}
