// --------------checkbox-start--------------
.ant-checkbox-wrapper:hover .ant-checkbox-inner,
.ant-checkbox:hover .ant-checkbox-inner,
.ant-checkbox-input:focus + .ant-checkbox-inner {
	border-color: @themeCheckboxHoverBorder !important;
}

.ant-checkbox .ant-checkbox-inner {
	background-color: @themeCheckboxInnerBackground !important;
	border: 1px solid @themeCheckboxInnerBorder !important;
}

.ant-checkbox-checked .ant-checkbox-inner {
	background-color: @themeCheckboxMainColor !important;
	border-color: @themeCheckboxMainColor !important;
}

/*  */
.ant-checkbox-indeterminate .ant-checkbox-inner::after {
	background-color: @themeCheckboxMainColor !important;
}

.ant-checkbox-disabled .ant-checkbox-inner {
	background-color: @themeCheckboxDisabledInner !important;
	border-color: @themeCheckboxInnerBorder !important;
}

.ant-checkbox-disabled.ant-checkbox-checked .ant-checkbox-inner::after {
	border-color: @themeCheckboxDisabledAfter !important;
}
.ant-checkbox-disabled:hover .ant-checkbox-inner {
	border-color: @themeCheckboxInnerBorder !important;
}

.ant-checkbox-wrapper {
	color: @themeCheckboxTextColor;
	font-size: 14px;
}

.ant-checkbox-disabled + span {
	color: @themeCheckboxTextDisabledColor;
	font-size: 14px;
}

// -------------checkbox-end----------------------