// -------------table-start----------------------
.ant-table {
	color: @tableText;
}
.ant-table-thead > tr > th {
	color: @tableHeadText;
	border-bottom: 1px solid @tableBorder;
}
.ant-table-thead > tr.ant-table-row-hover:not(.ant-table-expanded-row) > td,
.ant-table-tbody > tr.ant-table-row-hover:not(.ant-table-expanded-row) > td,
.ant-table-thead > tr:hover:not(.ant-table-expanded-row) > td,
.ant-table-tbody > tr:hover:not(.ant-table-expanded-row) > td {
	background: @tableHoverBg;
}
.ant-table-thead > tr > th .ant-table-column-sorter,
.ant-table-thead > tr > th .anticon-filter,
.ant-table-thead > tr > th .ant-table-filter-icon,
.ant-table-thead > tr > th .ant-table-column-sorter .ant-table-column-sorter-inner {
	color: @tableHeadIcon;
}

.ant-table-thead
	> tr
	> th.ant-table-column-has-actions.ant-table-column-has-filters:hover
	.anticon-filter:hover,
.ant-table-thead
	> tr
	> th.ant-table-column-has-actions.ant-table-column-has-filters:hover
	.ant-table-filter-icon:hover,
.ant-table-thead
	> tr
	> th.ant-table-column-has-actions.ant-table-column-has-filters
	.anticon-filter.ant-table-filter-open,
.ant-table-thead
	> tr
	> th.ant-table-column-has-actions.ant-table-column-has-filters
	.ant-table-filter-icon.ant-table-filter-open {
	color: @tableHeadIcon;
	background: @tableBg;
}

.ant-table-thead > tr > th .ant-table-filter-selected.anticon-filter {
	color: @themeColor;
}

.ant-table-tbody > tr.ant-table-expanded-row {
	background: @tableExpandedBg;
	&:hover {
		background: @tableExpandedBg;
	}

	& > td {
		background: @tableBg;
	}
}

.ant-table-fixed-header > .ant-table-content > .ant-table-scroll > .ant-table-body,
.ant-table-fixed-header .ant-table-scroll .ant-table-header,
.ant-table-fixed-left table,
.ant-table-fixed-right table,
.ant-table-footer {
	background: @tableBg;
}

.ant-table-fixed-header .ant-table-scroll .ant-table-header::-webkit-scrollbar,
.ant-table-bordered.ant-table-fixed-header .ant-table-scroll .ant-table-header::-webkit-scrollbar {
	border: 1px solid transparent;
}

.ant-table-thead > tr > th.ant-table-column-sort,
.ant-table-thead > tr > th.ant-table-column-has-actions.ant-table-column-has-sorters:hover,
.ant-table-thead
	> tr
	> th.ant-table-column-has-actions.ant-table-column-has-sorters:hover
	.anticon-filter,
.ant-table-thead
	> tr
	> th.ant-table-column-has-actions.ant-table-column-has-sorters:hover
	.ant-table-filter-icon {
	background: @tableSortColumn;
}

.ant-table-thead > tr,
.ant-table-thead > tr > th {
	background: @tableHeadBg;
}

.ant-table-tbody > tr:nth-of-type(2n) {
	& > td {
		background: @tableSecondRow;
	}
}

.ant-table-tbody > tr.ant-table-row-selected td {
	background: inherit;
}

.ant-table.ant-table-bordered .ant-table-title,
.ant-table.ant-table-bordered .ant-table-footer,
.ant-table-small,
.ant-table-bordered .ant-table-header > table,
.ant-table-bordered .ant-table-body > table,
.ant-table-bordered .ant-table-fixed-left table,
.ant-table-bordered .ant-table-fixed-right table {
	border: 1px solid @tableBorder;
}

.ant-table-bordered .ant-table-thead > tr:not(:last-child) > th {
	border-bottom: 1px solid @tableBorder;
}

.ant-table-footer::before {
	background: @tableBorder;
}

.ant-table-tbody > tr > td {
	border-bottom: 1px solid @tableBorder;
}
.ant-table-bordered .ant-table-thead > tr > th,
.ant-table-bordered .ant-table-tbody > tr > td {
	border-right: 1px solid @tableBorder;
}

.ant-table-row-expand-icon {
	background: @tableExpandIconBg;
	border-color: @tableExpandIconBorder;
}

.ant-table-filter-dropdown {
	background: @tableDropdowndBg;
}

.ant-table-placeholder {
	background: @tablePlaceholderBg;
}
// -------------table-end----------------------

@table-prefix-cls: ~'@{ant-prefix}-table';

.@{table-prefix-cls} {
	& .ant-table-placeholder {
		z-index: 0;
		display: flex;
		justify-content: center;
		border-bottom: 0;
	}

	& .ant-table-thead > tr > th,
	& .ant-table-tbody > tr > td {
		padding: 9px 13px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		border-bottom-width: 0;
	}

	& .ant-table-thead > tr > th.ant-table-selection-column,
	& .ant-table-tbody > tr > td.ant-table-selection-column {
		width: 46px;
		min-width: 46px;
	}

	& + .ant-table-pagination.ant-pagination {
		display: flex;
		justify-content: center;
		float: unset;
	}
}
