// --------------DatePicker-start------------
.ant-calendar {
	color: @datepicker-word-color;
	background-color: @datepicker-back-color;
	border-color: @datepicker-border-color;
}
.ant-calendar-picker:hover .ant-calendar-picker-input:not(.ant-input-disabled){
	border-color: @datepicker-input-hover;
}
.ant-calendar-picker:hover .ant-calendar-picker-input{
	border: 1px solid @datepicker-disabled-color;
}
.ant-calendar-picker:hover .ant-calendar-picker-clear{
	color: @datepicker-disabled-color;
	background: @datepicker-bg-color;
}
.ant-calendar-selected-day .ant-calendar-date{
	background-color: @themeColor;
}
.ant-calendar-input-wrap,
.ant-calendar-header {
	border-color: @datepicker-border-color;
}
.ant-calendar-range-middle {
	color: @datepicker-disabled-color;
}
.ant-calendar-selected-day .ant-calendar-date:hover{
	color: @datepicker-word-color;
	background: @themeColor;
}
.ant-calendar-month-panel{
	background: @datepicker-bg-color;
}
.ant-calendar-month-panel-header{
	border-color: @datepicker-line;
}
.ant-calendar-range .ant-calendar-body,
.ant-calendar-range .ant-calendar-month-panel-body,
.ant-calendar-range .ant-calendar-year-panel-body {
	border-color: @datepicker-border-color;
}
.ant-calendar-input,
.ant-calendar-range .ant-calendar-time-picker-input {
	color: @datepicker-word-color;
	background-color: @datepicker-back-color;
}
.ant-calendar-date {
	color: @datepicker-word-color;
}
.ant-calendar-last-month-cell .ant-calendar-date,
.ant-calendar-next-month-btn-day .ant-calendar-date {
	color: @datepicker-disabled-color;
}
.ant-calendar-header a {
	color: @datepicker-word-color;
}
.ant-calendar-header a:hover {
	color: @themeColor;
}
.ant-calendar-date:hover {
	color: @themeColor;
	background: @datepicker-back-color;
}
.ant-calendar-week-number .ant-calendar-body tr:hover {
	background: @datepicker-theme-color-opacity;
}
.ant-calendar-disabled-cell .ant-calendar-date {
	background: @datepicker-theme-color-opacity;
}
.ant-calendar-week-number .ant-calendar-body tr .ant-calendar-selected-day .ant-calendar-date {
	color: @datepicker-disabled-color;
}
.ant-calendar-week-number .ant-calendar-body tr.ant-calendar-active-week {
	background: @themeColor;
}
.ant-calendar-range .ant-calendar-in-range-cell:before {
	background-color: @themeColor;
	opacity: 0.2;
}
.ant-calendar-selected-date .ant-calendar-date,
.ant-calendar-selected-start-date .ant-calendar-date,
.ant-calendar-selected-end-date .ant-calendar-date {
	color: @datepicker-btn-color;
	background-color: @themeColor;
}
.ant-calendar-today .ant-calendar-date {
	color: @datepicker-word-color;
	border-color: @themeColor;
}
.ant-calendar-selected-date .ant-calendar-date:hover,
.ant-calendar-selected-start-date .ant-calendar-date:hover,
.ant-calendar-selected-end-date .ant-calendar-date:hover {
	color: @datepicker-word-color;
	background-color: @themeColor;
}
.ant-calendar-range .ant-calendar-year-panel,
.ant-calendar-range .ant-calendar-month-panel,
.ant-calendar-range .ant-calendar-decade-panel {
	background-color: @datepicker-back-color;
}
.ant-calendar-year-panel-selected-cell .ant-calendar-year-panel-year,
.ant-calendar-decade-panel-selected-cell .ant-calendar-decade-panel-decade,
.ant-calendar-month-panel-selected-cell .ant-calendar-month-panel-month {
	background-color: @themeColor;
}
.ant-calendar-year-panel-selected-cell .ant-calendar-year-panel-year:hover,
.ant-calendar-decade-panel-selected-cell .ant-calendar-decade-panel-decade:hover,
.ant-calendar-month-panel-selected-cell .ant-calendar-month-panel-month:hover {
	color: @datepicker-word-color;
	background-color: @themeColor;
}
.ant-calendar-month-panel-cell-disabled .ant-calendar-month-panel-month,
.ant-calendar-month-panel-cell-disabled .ant-calendar-month-panel-month:hover {
	background-color: @datepicker-back-color;
}
.ant-calendar-month-panel-cell-disabled .ant-calendar-month-panel-month:hover,
.ant-calendar-month-panel-cell-disabled .ant-calendar-month-panel-month:hover {
	color: @datepicker-word-color;
}
.ant-calendar-year-panel-year:hover,
.ant-calendar-decade-panel-decade:hover,
.ant-calendar-month-panel-month:hover {
	color: @themeColor;
	background-color: @datepicker-back-color;
}
.ant-calendar-footer{
	border-color: @datepicker-line;
}
.ant-calendar-footer a{
	color: @themeColor;
}
.ant-calendar-range-left .ant-calendar-time-picker-inner{
	border-color: @datepicker-line;
}
.ant-calendar-range-right .ant-calendar-time-picker-inner{
	border-color: @datepicker-line;
}
.ant-calendar-range.ant-calendar-time .ant-calendar-time-picker-combobox{
	background-color: @datepicker-bg-color;
}
.ant-calendar-time-picker-select{
	background-color: @datepicker-bg-color;
}
.ant-calendar-time-picker{
	background-color: @datepicker-bg-color;
}
li.ant-calendar-time-picker-select-option-selected{
	background-color: @datepicker-bg-color;
	border-top: 1px solid @datepicker-line;
	border-bottom: 1px solid @datepicker-line;
}
.ant-calendar-time-picker-select{
	border-color: @datepicker-line;
}
.ant-calendar-range.ant-calendar-time .ant-calendar-time-picker-combobox{
	border-color: @datepicker-line;
}
.ant-calendar .ant-calendar-ok-btn, .ant-calendar .ant-calendar-ok-btn:hover, .ant-calendar .ant-calendar-ok-btn:focus, .ant-tag-blue{
	color: @datepicker-btn-color;
	background-color: @themeColor;
	border-color: @themeColor;
}
.ant-calendar-picker:focus .ant-calendar-picker-input:not(.ant-input-disabled){
	border-color: @datepicker-input-hover;
	box-shadow: none;
}
.ant-calendar-time-picker-select li:hover{
	background: @datepicker-time-li-hover;
}
.ant-calendar-range-picker-separator{
	color: @datepicker-separator;
}
.ant-calendar-picker .ant-input{
	color: @datepicker-word-color;
}
// --------------DatePicker-end--------------