// --------------input-start------------
.ant-input, .ant-input-group-addon{
    background-color: @form-bg;
    border-color: @form-border;
    color: @form-hover-border;
}
.ant-input-group{
    color: @form-hover-border;
    .ant-select-selection-selected-value, .ant-select-arrow-icon{
        color: @form-hover-border;
    }
}

// --------------input-end--------------