.ant-spin {
	#spin {
		.spinOne {
			width: 71px;
			animation: rotation 1.2s linear infinite;
		}
		@keyframes rotation {
			from {
				-webkit-transform: rotate(0deg);
			}
			to {
				-webkit-transform: rotate(360deg);
			}
		}
	}
}
.ant-spin-sm {
	#spin {
		.spinOne {
			width: 41px;
		}
	}
}
.ant-spin-lg {
	#spin {
		.spinOne {
			width: 101px;
		}
	}
}
.ant-spin-text {
	top: 70% !important;
}
