/* select */
.ant-select-selection {
	border-radius: 2px;
	box-shadow: none;
	background: @selection-bg-color;
	border: 1px solid @selection-border-color;
}
.ant-select-dropdown-menu {
	background: @selection-bg-color;
}
.ant-select-selection-selected-value, .ant-select-dropdown-menu-item, .ant-select-arrow-icon, .ant-select-remove-icon, .anticon-plus {
	color: @inputTextColor;
}
.ant-select-dropdown-menu-item-disabled {
	color: @promptTextColor;
}
.ant-select-dropdown-menu-item-disabled:hover {
	color: @promptTextColor;
}
.ant-select-disabled .ant-select-selection {
	background: @select-disabled-bg;
}
.ant-select-disabled {
	.ant-select-arrow-icon {
		color: @promptTextColor;
	}
}
.ant-select-disabled .ant-select-selection-selected-value {
	color: @promptTextColor;
}
.ant-select-dropdown-menu-item:hover:not(.ant-select-dropdown-menu-item-disabled) {
	background: @selection-hover-bg;
}
.ant-select-selection__rendered {
	margin-right: @control-padding-horizontal - 2px;
	margin-left: @control-padding-horizontal - 2px;
}
.ant-select-dropdown-menu-item-group-title {
	color: @inputTextColor;
}
.ant-select-selection:hover {
	border: 1px solid @selection-hover-border;
}
.ant-select-focused .ant-select-selection,
.ant-select-selection:focus,
.ant-select-selection:active {
	border: 1px solid @selection-focus-border;
	box-shadow: none;
}
.ant-select-selection--multiple .ant-select-selection__choice {
	margin-right: 5px;
	margin-left: 5px;
	color: @inputTextColor;
	background-color: @multiple-selection-bg;
	border-color: @multiple-selection-border;
}
.ant-select-dropdown-menu::-webkit-scrollbar {
	width: 6px;
}
.ant-select-dropdown-menu::-webkit-scrollbar-thumb {
	background-color: @scrollbarBg;
	border-radius: 3px;
}
.ant-select-dropdown-menu-item-selected, .ant-select-dropdown-menu-item-active {
	background-color: @selection-selected-bg;
}
.ant-select-dropdown {
	background: @selection-bg-color;
	color: @inputTextColor;
}
.ant-divider-horizontal {
	background: @multiple-selection-border;
}
.ant-select-focused {
	color: @inputTextColor;
}
.ant-select-dropdown-menu-item-active:not(.ant-select-dropdown-menu-item-disabled) {
	background: @selection-selected-bg;
}
/* select */