/* select-tree start */
.ant-select-tree {
	background: @tree-bg;
}
.ant-select-tree li .ant-select-tree-node-content-wrapper {
	color: @tree-node-color;
}
.ant-select-tree li .ant-select-tree-node-content-wrapper:hover {
	background-color: @tree-hover-bg;
}
.ant-select-tree li .ant-select-tree-node-content-wrapper.ant-select-tree-node-selected {
	background-color: @tree-select-bg;
	.ant-select-tree-title {
		color: rgba(0, 162, 208, 1);
	}
}
.ant-select-tree-checkbox-inner {
	background-color: @tree-check-bg;
	border: 1px solid @tree-check-color;
}
.ant-select-tree-checkbox-wrapper:hover .ant-select-tree-checkbox-inner,
.ant-select-tree-checkbox:hover .ant-select-tree-checkbox-inner,
.ant-select-tree-checkbox-input:focus + .ant-select-tree-checkbox-inner {
	border-color: @tree-check-border;
}
.ant-select-switcher-icon {
	color: @tree-switcher-icon !important;
}
.ant-select-tree.ant-select-tree-show-line li span.ant-select-tree-switcher {
	color: @tree-line-color !important;
	background: @tree-line-bg !important;
}
.ant-select-tree.ant-select-tree-directory
	> li
	span.ant-select-tree-node-content-wrapper:hover::before,
.ant-select-tree.ant-select-tree-directory
	.ant-select-tree-child-select-tree
	> li
	span.ant-select-tree-node-content-wrapper:hover::before {
	background: @tree-directory-hover;
}
.ant-select-tree.ant-select-tree-directory
	> li.ant-select-tree-select-treenode-selected
	> span.ant-select-tree-node-content-wrapper::before,
.ant-select-tree.ant-select-tree-directory
	.ant-select-tree-child-select-tree
	> li.ant-select-tree-select-treenode-selected
	> span.ant-select-tree-node-content-wrapper::before {
	background: @tree-directory-selected;
}
.ant-select-dropdown-search {
	background: @tree-check-bg;
}
.ant-select-tree-dropdown .ant-select-dropdown-search .ant-select-search__field {
	background: @tree-check-bg;
	border: 1px solid @tree-check-color;
}
/* select-tree end */

.ant-select-tree-checkbox-checked .ant-select-tree-checkbox-inner {
	background-color: rgba(0, 162, 208, 1);
	border-color: rgba(0, 162, 208, 1);
}
.ant-select-tree-checkbox-indeterminate .ant-select-tree-checkbox-inner::after {
	background-color: rgba(0, 162, 208, 1);
}
