/* cascader */
.ant-cascader-picker {
	background: @cascader-picker-bg;
}
.ant-cascader-input.ant-input:hover {
    border: 1px solid @cascader-border-hover;
}
.ant-cascader-input.ant-input:focus {
    border: 1px solid @cascader-border-focus;
}
.ant-cascader-picker:focus .ant-cascader-input {
	box-shadow: none;
}
.ant-cascader-input {
	border-radius: 2px;
}
.ant-cascader-menu {
	box-shadow: 0 1px 3px 0px rgba(0, 0, 0, 0.1);
	background: @cascader-picker-bg;
	color: @cascader-menu-content;
}
.ant-cascader-menu-item-expand-icon {
	color: @cascader-menu-content;
}
.ant-input::-webkit-input-placeholder {
	color: @cascader-input-content;
}
.ant-cascader-picker-arrow {
	color: @cascader-input-content;
}
.ant-cascader-menu-item-active:not(.ant-cascader-menu-item-disabled), .ant-cascader-menu-item-active:not(.ant-cascader-menu-item-disabled):hover {
    background-color: @cascader-item-active;
}
.ant-cascader-menu-item:hover {
	background-color: @cascader-menu-hover;
}
.ant-cascader-picker-label {
	color: @cascader-menu-content;
}
.ant-cascader-picker-clear {
	background: @cascader-picker-clear-bg;
	color: @cascader-picker-clear-color;
}
.ant-cascader-menu-item-disabled {
	color: @cascader-disabled-content;
}
.ant-cascader-menu-item-disabled:hover {
	background: @cascader-picker-bg;
}
/* cascader */


@cascader-prefix-cls: ~'@{ant-prefix}-cascader';

.@{cascader-prefix-cls} {
	.reset-component;

	&-input.@{ant-prefix}-input {
		position: relative;
		width: 100%;
		// Add important to fix https://github.com/ant-design/ant-design/issues/5078
		// because input.less will compile after cascader.less
		background-color: transparent !important;
		cursor: pointer;
	}

	&-picker-show-search &-input.@{ant-prefix}-input {
		position: relative;
	}

	&-picker {
		.reset-component;

		position: relative;
		display: inline-block;
		background-color: @component-background;
		border-radius: @border-radius-base;
		outline: 0;
		cursor: pointer;
		transition: color 0.3s;

		&-with-value &-label {
			color: transparent;
		}

		&-disabled {
			color: @disabled-color;
			background: @input-disabled-bg;
			cursor: not-allowed;
			.@{cascader-prefix-cls}-input {
				cursor: not-allowed;
			}
		}

		// &:focus .@{cascader-prefix-cls}-input {
		//   .active;
		// }

		&-show-search&-focused {
			color: @disabled-color;
		}

		&-label {
			position: absolute;
			top: 50%;
			left: 0;
			width: 100%;
			height: 20px;
			margin-top: -10px;
			padding: 0 @control-padding-horizontal;
			overflow: hidden;
			line-height: 20px;
			white-space: nowrap;
			text-overflow: ellipsis;
		}

		&-clear {
			position: absolute;
			top: 50%;
			right: @control-padding-horizontal;
			z-index: 2;
			width: 12px;
			height: 12px;
			margin-top: -6px;
			color: @disabled-color;
			font-size: @font-size-sm;
			line-height: 12px;
			background: @component-background;
			cursor: pointer;
			opacity: 0;
			transition: color 0.3s ease, opacity 0.15s ease;
			&:hover {
				color: @text-color-secondary;
			}
		}

		&:hover &-clear {
			opacity: 1;
		}

		// arrow
		&-arrow {
			position: absolute;
			top: 50%;
			right: @control-padding-horizontal;
			z-index: 1;
			width: 12px;
			height: 12px;
			margin-top: -6px;
			color: @disabled-color;
			font-size: 12px;
			line-height: 12px;
			transition: transform 0.2s;
			&&-expand {
				transform: rotate(180deg);
			}
		}
	}

	&-picker-small &-picker-clear,
	&-picker-small &-picker-arrow {
		right: @control-padding-horizontal-sm;
	}

	&-menus {
		position: absolute;
		z-index: @zindex-dropdown;
		font-size: @font-size-base;
		white-space: nowrap;
		background: @component-background;
		border-radius: @border-radius-base;
		box-shadow: @box-shadow-base;

		ul,
		ol {
			margin: 0;
			padding: 0;
			list-style: none;
		}

		&-empty,
		&-hidden {
			display: none;
		}
		&.slide-up-enter.slide-up-enter-active&-placement-bottomLeft,
		&.slide-up-appear.slide-up-appear-active&-placement-bottomLeft {
			animation-name: antSlideUpIn;
		}

		&.slide-up-enter.slide-up-enter-active&-placement-topLeft,
		&.slide-up-appear.slide-up-appear-active&-placement-topLeft {
			animation-name: antSlideDownIn;
		}

		&.slide-up-leave.slide-up-leave-active&-placement-bottomLeft {
			animation-name: antSlideUpOut;
		}

		&.slide-up-leave.slide-up-leave-active&-placement-topLeft {
			animation-name: antSlideDownOut;
		}
	}
	&-menu {
		display: inline-block;
		min-width: 111px;
		height: 180px;
		margin: 0;
		padding: 0;
		overflow: auto;
		vertical-align: top;
		list-style: none;
		border-right: @border-width-base @border-style-base @border-color-split;
		-ms-overflow-style: -ms-autohiding-scrollbar; // https://github.com/ant-design/ant-design/issues/11857

		&:first-child {
			border-radius: @border-radius-base 0 0 @border-radius-base;
		}
		&:last-child {
			margin-right: -1px;
			border-right-color: transparent;
			border-radius: 0 @border-radius-base @border-radius-base 0;
		}
		&:only-child {
			border-radius: @border-radius-base;
		}
	}
	&-menu-item {
		padding: 5px @control-padding-horizontal;
		line-height: 22px;
		white-space: nowrap;
		cursor: pointer;
		transition: all 0.3s;
		&:hover {
			background: @item-hover-bg;
		}
		&-disabled {
			color: @disabled-color;
			cursor: not-allowed;
			&:hover {
				background: transparent;
			}
		}
		&-active:not(&-disabled) {
			&,
			&:hover {
				font-weight: 600;
				background: @background-color-base;
			}
		}
		&-expand {
			position: relative;
			padding-right: 24px;
		}

		&-expand &-expand-icon,
		&-expand &-loading-icon {
			.iconfont-size-under-12px(10px);

			position: absolute;
			right: @control-padding-horizontal;
			color: @text-color-secondary;
		}

		& &-keyword {
			color: @highlight-color;
		}
	}
}