// --------------TimePicker-start------------
.ant-time-picker-input:hover{
	border-color: @themeColor;
}
.ant-time-picker-panel-select li:hover{
	background: @timepicker-li-bg-color;
}
.ant-time-picker-panel-combobox, li.ant-time-picker-panel-select-option-selected{
	background-color: @timepicker-bg-color;
	border-color: @timepicker-border-color;
}
.ant-time-picker-panel-input-wrap{
	background-color: @timepicker-bg-color;
	border-bottom-color: @timepicker-border-color;
	input{
		color: @timepicker-color;
		background-color: @timepicker-bg-color;
	}
}
.ant-time-picker-input{
	color: @timepicker-color;
	background-color: @timepicker-bg-color;
	border-color: @timepicker-border-color;
}
.ant-time-picker-panel{
	color: @timepicker-color;
}
.ant-time-picker-panel-select{
	border-left-color: @timepicker-border-color;
}
// --------------TimePicker-end--------------