#spinIcon {
	position: relative;
	width: 72px;
	height: 72px;

	img {
		position: absolute;
		top: 36px;
		left: 36px;
		width: 0;
		opacity: 0;
	}

	.IconOut {
		animation: showA 3s linear infinite;
	}

	.IconMid {
		animation: showB 3s linear infinite;
	}

	.IconIn {
		animation: showC 3s linear infinite;
	}

	@keyframes showA {
		0% {
			width: 0;
			transform: rotate(-180deg);
			opacity: 0;
		}

		25% {
			top: 0;
			left: 0;
			width: 72px;
			transform: rotate(0deg);
			opacity: 1;
		}

		50% {
			top: 0;
			left: 0;
			width: 72px;
			transform: rotate(0deg);
			opacity: 1;
		}

		75% {
			top: 0;
			left: 0;
			width: 72px;
			transform: rotate(0deg);
			opacity: 1;
		}

		100% {
			top: 0;
			left: 0;
			width: 72px;
			transform: rotate(0deg);
			opacity: 0;
		}
	}

	@keyframes showB {
		0% {
			opacity: 0;
		}

		25% {
			top: 36px;
			left: 36px;
			width: 0;
			transform: rotate(-180deg);
			opacity: 0;
		}

		50% {
			top: 15px;
			left: 14.5px;
			width: 43px;
			transform: rotate(0deg);
			opacity: 1;
		}

		75% {
			top: 15px;
			left: 14.5px;
			width: 43px;
			transform: rotate(0deg);
			opacity: 1;
		}

		100% {
			top: 15px;
			left: 14.5px;
			width: 43px;
			transform: rotate(0deg);
			opacity: 0;
		}
	}

	@keyframes showC {
		0% {
			opacity: 0;
		}

		25% {
			opacity: 0;
		}

		50% {
			top: 36px;
			left: 36px;
			width: 0;
			transform: rotate(-180deg);
			opacity: 0;
		}

		75% {
			top: 26px;
			left: 25px;
			width: 21px;
			transform: rotate(0deg);
			opacity: 1;
		}

		100% {
			top: 26px;
			left: 25px;
			width: 21px;
			transform: rotate(0deg);
			opacity: 0;
		}
	}
}
