
// --------------tabs-start--------------
.ant-tabs {
	color: @themeTabsContent;
}
.ant-tabs .ant-tabs-nav {
	color: @themeTabsText;
}

.ant-tabs-nav .ant-tabs-tab-active {
	color: @themeColor;
}

.ant-tabs .ant-tabs-nav .ant-tabs-tab-disabled,
.ant-tabs .ant-tabs-nav .ant-tabs-tab-disabled:hover {
	color: @themeTabsTextDisabled;
}

.ant-tabs-ink-bar {
	background-color: @themeColor;
}

.ant-tabs-bar {
	border-bottom: 1px solid @themeTabsBottomLine;
}

// --------------end-start--------------

@tab-prefix-cls: ~'@{ant-prefix}-tabs';

.@{tab-prefix-cls} {
	& .ant-tabs-nav {
		margin: 0;
	}

	& .ant-tabs-nav .ant-tabs-tab {
		position: relative;
		margin: 0 16px;
		padding: 8px 2px;
	}
}