// ---------radio-start----------------
.ant-radio-inner {
	background-color: @themeRadioInnerBackground;
	border-color: @themeRadioInnerBorder;
}
.ant-radio-checked .ant-radio-inner {
	border-color: @themeRadioCheckedColor;
}

.ant-radio-inner::after {
	background-color: @themeRadioCheckedColor;
}

.ant-radio-disabled .ant-radio-inner {
	background-color: @themeRadioDisabledBackground;
	border-color: @themeRadioDisabledBorder;
}

.ant-radio-disabled .ant-radio-inner::after {
	background-color: @themeRadioDisabledBorder;
}
.ant-radio:hover .ant-radio-inner {
	border-color: @themeRadioHoverBorder;
}
.ant-radio-group-solid .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
	background: @themeRadioCheckedColor;
	border-color: @themeRadioCheckedColor;
}
.ant-radio-group-solid
	.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):hover {
	background: @themeRadioCheckedColor;
	border-color: @themeRadioCheckedColor;
}
.ant-radio-button-wrapper:hover {
	color: @themeRadioCheckedColor;
}
.ant-radio-button-wrapper-checked {
	color: @themeRadioCheckedColor;
	border-color: @themeRadioCheckedColor;
	-webkit-box-shadow: none;
	box-shadow: none;
	outline: none;
}
.ant-radio-button-wrapper-checked:hover {
	color: @themeRadioCheckedColor;
	border-color: @themeRadioCheckedColor;
	-webkit-box-shadow: none;
	box-shadow: none;
	outline: none;
}
.ant-radio-button-wrapper-checked:first-child {
	border-color: @themeRadioCheckedColor;
}
.ant-radio-input:focus + .ant-radio-inner {
	-webkit-box-shadow: none;
	box-shadow: none;
	outline: none;
}

.ant-radio-wrapper {
	color: @themeRadioTextColor;
	font-size: 14px;
}
.ant-radio-disabled + span {
	color: @themeRadioTextDisabledColor;
	font-size: 14px;
}

// ----------radio-end------------------------