.ant-modal {
  color: @text-content;
  .ant-modal-header {
    border-bottom-color: @modal-border;
    background: @modal-background;
    .ant-modal-title {
      text-align: center;
      color: @text-title;
    }
  }

  .ant-modal-body {
    padding: 10px 20px;
    background: @modal-background;
  }

  .ant-modal-footer {
    border-top-width: 0;
    background: @modal-background;

    & .ant-btn {
      border-radius: 2px;
    }
  }

  .ant-modal-close {
    color: @text-content;
  }

  .ant-modal-confirm-title {
    color: @text-title;
  }

  .ant-modal-confirm-content {
    color: @text-content;
  }

  .ant-modal-confirm-btns .ant-btn {
    border-radius: 2px;
  }
}
