

/****  button ********/
.ant-btn {
	background-color: transparent;
	border-color: @button-primary;
	color: @button-primary;
	&:hover {
		background-color: transparent;
		border-color: @button-primary;
		color: @button-primary;
	}
	&:active,
	&:focus {
		background-color: transparent;
		border-color: @button-primary;
		color: @button-primary;
	}
}
.ant-btn-primary {
	background-color: @button-primary;
	border-color: @button-primary;
	color: #fff;
	&:hover {
		border-color: @button-primary-hover;
		background-color: @button-primary-hover;
		color: #fff;
	}
	&:active,
	&:focus {
		background-color: @button-primary-active;
		border-color: @button-primary-active;
		color: #fff;
	}
}
.ant-btn-danger {
	background-color: @button-danger;
	border-color: @button-danger;
	color: #fff;
	&:hover {
		border-color: @button-danger-hover;
		background-color: @button-danger-hover;
		color: #fff;
	}
	&:active,
	&:focus {
		background-color: @button-danger-active;
		border-color: @button-danger-active;
		color: #fff;
	}
}
.ant-btn-link {
	color: @button-primary;
	background-color: transparent;
	border-color: transparent;
	-webkit-box-shadow: none;
	box-shadow: none;
	&:hover {
		background-color: transparent;
		border-color: transparent;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	&:active,
	&:focus {
		background-color: transparent;
		border-color: transparent;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
}
/* -------button-----------*/