@import (reference) '../../mixins/index.less';

.tzzy-message {
	.reset-component;
	position: fixed;
	top: 16px;
	left: 0;
	z-index: @zindex-message;
	width: 100%;
	pointer-events: none;

	&-notice {
		padding: 8px;
		text-align: center;
		&:first-child {
			margin-top: -8px;
		}
	}

	&-success,
	&-error,
	&-warning,
	&-info,
	&-loading {
		display: inline-flex;
		min-width: 160px;
		max-width: 30%;
		padding: 28px 35px;
		color: @white;
		white-space: pre-wrap;
		text-align: left;
		background: rgba(0, 0, 0, 0.6);
		border-radius: @border-radius-base;
		box-shadow: @shadow-3;
		pointer-events: all;
	}

	&-success .@{iconfont-css-prefix} {
		color: @success-color;
	}

	&-error .@{iconfont-css-prefix} {
		color: @error-color;
	}

	&-warning .@{iconfont-css-prefix} {
		color: @warning-color;
	}

	&-info .@{iconfont-css-prefix},
	&-loading .@{iconfont-css-prefix} {
		color: @info-color;
	}

	.@{iconfont-css-prefix} {
		position: relative;
		top: 0;
		margin-right: 8px;
		font-size: 24px;
	}

	&-notice.move-up-leave.move-up-leave-active {
		overflow: hidden;
		animation-name: MessageMoveOut;
		animation-duration: 0.3s;
	}
}

@keyframes MessageMoveOut {
	0% {
		max-height: 150px;
		padding: 8px;
		opacity: 1;
	}
	100% {
		max-height: 0;
		padding: 0;
		opacity: 0;
	}
}
