@import (reference) '../../style/themes/default.less';
@import (reference) '../../style/themes/theme.less';
@import (reference) '../../style/themes/dark-default.less';
@import (reference) '../../style/themes/light-default.less';
@import (reference) '../../style/themes/components/input.less';
@color: #767676;
// @TODO focus-color
@focus-color: #00a2d0;
.ant-btn .anticon {
	font-size: 16px;
}
.ant-input {
	color: @color;
}
.code-box-demo {
	.ant-input,
	.ant-input-group-addon,
	textarea.ant-input {
		&:hover {
			border-color: @color;
		}
		&:focus {
			border-color: @focus-color;
			color: @color;
			outline: 0;
			box-shadow: 0 0 0 2px rgba(0, 162, 208, 0.2);
		}
	}
}

.ant-input-affix-wrapper:hover .ant-input:not(.ant-input-disabled) {
	border-color: @color;
	&:focus {
		border-color: @focus-color;
		color: @color;
		outline: 0;
		box-shadow: 0 0 0 2px rgba(0, 162, 208, 0.2);
	}
}
.ant-input-group-addon {
	color: #d6d8d8;
	background: #000;
}
.theme-dark {
	.ant-input,
	.ant-input-group-addon,
	textarea.ant-input {
		&:hover {
			border-color: @color;
		}
		&:focus {
			color: #d6d8d8;
			border-color: @focus-color;
			outline: 0;
			box-shadow: 0 0 0 2px rgba(0, 162, 208, 0.2);
		}
	}
	.ant-input-search-enter-button + .ant-input-group-addon .ant-input-search-button,
	.ant-input-search-enter-button input + .ant-input-group-addon .ant-input-search-button {
		color: #d6d8d8;
		background: #000;
		border-color: #454545;
	}
}
