/* tree start */
.ant-tree {
	background: @tree-bg;
}
.ant-tree li .ant-tree-node-content-wrapper {
	color: @tree-node-color;
}
.ant-tree li .ant-tree-node-content-wrapper:hover {
	background-color: @tree-hover-bg;
}
.ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-selected {
	background-color: @tree-select-bg;
}
.ant-tree-checkbox-indeterminate .ant-tree-checkbox-inner {
	background-color: @tree-indeterminate-bg;
	border-color: @tree-indeterminate-border;
}
.ant-tree-checkbox-inner {
	background-color: @tree-check-bg;
	border: 1px solid @tree-check-color;
}
.ant-tree-checkbox-wrapper:hover .ant-tree-checkbox-inner,
.ant-tree-checkbox:hover .ant-tree-checkbox-inner,
.ant-tree-checkbox-input:focus + .ant-tree-checkbox-inner {
	border-color: @tree-check-border;
}
.ant-tree-checkbox-disabled .ant-tree-checkbox-inner {
	background-color: @tree-check-dis-bg !important;
	border-color: @tree-check-color !important;
}
.ant-tree-checkbox-disabled.ant-tree-checkbox-checked .ant-tree-checkbox-inner::after {
	border-color: @tree-check-dis-border;
}
li.ant-tree-treenode-disabled > span:not(.ant-tree-switcher),
li.ant-tree-treenode-disabled > .ant-tree-node-content-wrapper,
li.ant-tree-treenode-disabled > .ant-tree-node-content-wrapper span {
	color: @tree-treenode-dis-border;
}
.ant-tree-switcher-icon {
	color: @tree-switcher-icon !important;
}
.ant-tree.ant-tree-show-line li span.ant-tree-switcher {
	color: @tree-line-color !important;
	background: @tree-line-bg !important;
}
.ant-tree.ant-tree-directory > li span.ant-tree-node-content-wrapper:hover::before,
.ant-tree.ant-tree-directory
	.ant-tree-child-tree
	> li
	span.ant-tree-node-content-wrapper:hover::before {
	background: @tree-directory-hover;
}
.ant-tree.ant-tree-directory
	> li.ant-tree-treenode-selected
	> span.ant-tree-node-content-wrapper::before,
.ant-tree.ant-tree-directory
	.ant-tree-child-tree
	> li.ant-tree-treenode-selected
	> span.ant-tree-node-content-wrapper::before {
	background: @tree-select-bg;
}
/* tree end */

.ant-tree-checkbox-checked .ant-tree-checkbox-inner {
	background-color: rgba(0, 162, 208, 1);
	border-color: rgba(0, 162, 208, 1);
}
.ant-tree-checkbox-indeterminate .ant-tree-checkbox-inner::after {
	background-color: rgba(0, 162, 208, 1);
}
.ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-selected {
	.ant-tree-title {
		color: rgba(0, 162, 208, 1);
	}
}
.ant-tree-node-selected .ant-tree-iconEle{
	color: #00a2d0;
}